<template lang="html">
    <li class="row text-white list-group-item bg-dark d-flex" :class="{marcada: todo.checked}">
      <div class="col-2 col-md-1 checkbox-div d-flex align-items-center justify-content-center">
        <label class="labelCheck img-fluid" :class="todo.checked ? 'checked' : 'unchecked'">
          <input v-model="todo.checked" id="checkbox" name="checkbox" type="checkbox" autocomplete="off">
        </label>
      </div>
      <div class="col-8 col-md-10">
        <div class="row">
          <h4 :class="{notaMarcada: todo.checked}">{{todo.text}}</h4>
        </div>
        <div class="row">
          <div class="mr-2">
            Prioridad:
          </div>
          <div class="btn-group btn-group-sm d-flex flex-row align-items-center" role="group">
            <button type="button" class="btn btn-info" :class="obtenerClase(1,todo)" @click="changePriority(todo,1)">Baja</button>
            <button type="button" class="btn btn-primary" :class="obtenerClase(2,todo)" @click="changePriority(todo,2)">Media</button>
            <button type="button" class="btn btn-danger" :class="obtenerClase(3,todo)" @click="changePriority(todo,3)">Alta</button>
          </div>
        </div>
      </div>
      <div class="eliminar col-2 col-md-1 d-flex align-items-center justify-content-center">
        <div class=" rounded btn-danger d-flex align-items-center justify-content-center" @click="borrar(todo)">
          <img class="img-fluid rounded icono-eliminar" src="../assets/delete-icon.png">
        </div>
      </div>
    </li>
</template>

<script lang="js">

  export default  {
    name: 'nota',
    props: ['todo','index'],
    mounted () {
    
    },
    methods: {
      borrar: function(todo){
        this.$emit('click',todo);
      },
      changePriority: function(todo,priority){
        todo.priority = priority;
        this.$emit('prioridadCambiada');
      },
      obtenerClase: function(prioridad,todo){
        return prioridad == todo.priority ? "" : "prioridad-inactiva";
      }
    },
    computed: {

    }
}
</script>

