body{
    min-height:100vh;
}
#eliminar{
    cursor:pointer;
    padding:0;
}
ul{
    padding-left:0;
}
#contenedor{
    flex:1 0 auto;
}
#app-4{
    min-height:100vh;
}
hr{
    padding:0;
}
@media (max-width: 850px){
    h1{
        font-size: 30px!important;
    }
    #texto::placeholder{
        font-size: 15px!important;
    }
}
</style>
